<script>
import HeaderComponent from "@/components/Header1/header1.vue";
export default {
  name: "emergencyRepairOrderViewSignOffDetailsComponent",
  components: { HeaderComponent },
  data() {
    return {
      data: [],
      form: {},
      workPerson: [],
      pdfs: [],
    };
  },
  computed: {},
  methods: {},
  async mounted() {
    //this.data = JSON.parse(this.$route.query.data);
    const prjNo = this.$route.query.data;
    if (prjNo == null) {
      this.prototype.$message({
        type: "error",
        duration: 5000,
        showClose: true,
        message: `系統異常: 沒有參數單號`,
      });
      return;
    }
    try {
      const res = await this.$store.dispatch("getPrintFormInfo", prjNo);
      if (res.data.isOK) {
        this.data = res.data.data;
        if (this.data) this.$root.overlay = false;
        this.form = this.data.form;
        this.workPerson = this.data.work_Person;
        this.pdfs = this.data.pdfs;
        this.$store.commit("HEADER_HANDLER", false);
        this.$store.commit("BREADCRUMB_HANDLER", false);
        this.$root.$children[0].sidebarSwitch = false;
        const container = document.querySelector(".el-container");
        container.style.height = "initial";
      }
    } catch (err) {
      console.log(err);
    }
  },
};
</script>

<template src="./emergencyRepairOrderViewSignOffDetails.html"></template>
